body {
    max-width: 1920px;
    width: $standart-100P;
    margin: 0 auto;

    font-family: 'Montserrat', sans-serif;
    font-size: pxToRem(21);
    color: $c-Coffee;
}

*,
*:before,
*:after {
    outline: none;
    box-sizing: border-box;
}

* a {
    text-decoration: none;
}

h1,h2,h3,h4 {
    margin: 0;
}

.general-title {
    font-family: 'Raleway Medium';
    font-weight: normal;
    font-size: pxToRem(38);
}

.general-title--black {
    color: $c-black;
}

.general-title--beige {
    color: $c-Calico;
}

hr {
    background-color: $c-white;
    height: 55px; 
    width: 2px;
    border-width: 0;
}











