// Content ---------------------------------------

.content {
    position: relative;

    .content { 
        &__conteiner {
            height: 666px;
            @include displayFlex();
            margin-top: $space-4P;
            margin-left: $space-8P;

            .circle_01 {
                position: absolute;
                left: 72%;
                top: 13.3%;
                width: 28%;
            }
            
            .circle_01 img {
                width: $standart-100P;
                position: absolute;
            }
            
            .circle_02 {
                position: absolute;
                width: 23%;
                top: 28%;
                left: 27.6%;
            }
            
            .circle_02 img {
                width: $standart-100P;
            }
            
        }
        
        &__avatar {
            position: relative;
            max-width: 555px;
            height: 610px;

            img:nth-child(1) {
                position: absolute;
                right: 44%;
                bottom: 31.5%;
                width: 100.5%;
            }
            
            img:nth-child(2) {
                max-width: $standart-100P;
                min-height: $standart-100P;
            }
            
            img {
                border-radius: $space-xxl-small;
            }
    
        }
        
    }
    
    .info {
        position: relative;
        width: 53%;
        margin: $space-5P 0 0 $space_-7P;

        .content__avatar {
            display: none;
        }
        
        .info {

            &__title {
                @include displayFlex();
                border-radius: $space-x-small;
                height: 295px;
                background-color:$c-whiteOpaciti_68;
                @include boxShadow(3px 3px 9px -2px $c-Gray);
            }
            
            &__name {
                margin-top: $space-standart;
                margin-left: $space-3P;
                width: 64%;

                h2, 
                h3,
                &__contact h2 {
                    text-align: left;
                }

                h2 {
                    width: 160%;
                }
                
                :nth-child(4) {
                margin-top: 0;
                padding-bottom: $space-xxx-small;
                }
                
            }
             
            &__link {

                a {
                    color: $c-Coffee;
                }

                img {
                    width: 70px;
                    @include transition(.3s)
                }
                
                img:hover {
                    @include hoverEffect()
                }

                @include displayFlex();
                @include flexAliginItems(end, end, flex-end);

                > div{
                        @include displayFlex();
                        @include flexAliginItems(center, center, center);

                    > a {
                        @include displayFlex();
                        @include flexAliginItems(center, center, center);
                    }

                    div {
                        width: 19vh;
                    }
    
                    a:first-child:hover {
                        @include hoverEffect()
                    }
                    
                    
                }
                
            
            }

            &__contact, 
            &__contact h2 {
                @include displayFlex();
                @include flexDirection(vertical,normal,column);
                text-align: right;
                margin-top: $space-standart;
            }
            
            &__contact p {
                margin-bottom: $space-medium;
            }
            
            &__but :first-child {
                margin-right: $space-4P;
            }
        }
    }
    
    
    
    .about {

        margin-top: $space-4P;
        @include displayFlex();
        @include flexJustifyContent(space-evenly,space-evenly,space-evenly);
        position: relative;

        h2 {
            margin: $space-6P 0 0 ;
        }

        .about {

            &__me {

                margin-left: $space-5P;

                p {
                    text-align: justify;
                }
                
                :first-child {
                    max-width: 592px;
                }
    
            }
            
           
            &__interests {
                max-width: 592px;
        
                img {
                    max-width: $standart-100P;
                    min-height: $standart-100P;
                    @include transition(.4s);
                }
        
                div {
                    &:nth-child(2) {
                        @include displayFlex();
                        @include flexAliginItems(center,center,center);
                        @include flexJustifyContent(start,start, flex-start);

                        div {
                            width: 270px;
                            height: $standart-100;
                        }
                        
                        p {
                            text-align: right;
                            font-weight: 900;
                            width: $standart-100P;
                        }
                    }
                    
                }

                ul {
                    padding-left: 0;

                    > li {
                    list-style-type: none;

                        :before {
                            content: '\2022';
                            font-size: x-large;
                            padding-right: $space-x-small;
                        }
                    }
                }
                
                img {
                    width: 185px;
                }
            
            }

            &__skills {

                margin-left: -3.5%;
                width: 760px;
                @include boxShadow(3px 3px 9px -2px $c-Gray);
                border-radius: $space-xxl-small;
                height: 685px;

                > div {
                    margin-left: $space-10P;
                }
                
                img {
                    position: absolute;
                    left: 72%;
                    top: 31.6%;
                    width: 28%;
                }

                &-empty {
                    width: 70%;
                    margin-top: $space-15P;
                    position: relative;
                    top: -30px;

                    div {
                        background-color: $c-aliceblue;
                        margin-bottom: $space-standart;
                        height: 47px;
                        border-radius: 8px;
                        width: $standart-100P;
                    }
                }
                
                &-name {
                    position: relative;
                    bottom: 433px;
                    width: 70%;

                    div {
                        background-image: url(../../../../assets//elements/_elementy_gradient_menu.png);
                        width: 39%;
                        background-repeat: no-repeat;
                        background-position: 17% 33%;
                        margin-bottom: $space-standart;
                        height: 47px;
                        border-radius: 8px;
                        background-size: 129% 273%;
                        @include flexAliginItems(center,center,center);
                        @include displayFlex();
                        @include flexJustifyContent(end,end,flex-end);
                        @include boxShadow(3px 3px 9px -2px $c-Gray);
                    }
                    
                    .ts {
                        width: 20%;
                        min-width: 185px;
                    }
                    
                    .js {
                        width: 60%;
                    }
                    
                    .html {
                        width: 81%;
                    }
                    
                    .nodejs {
                        width: 1%;
                        min-width: 147px;
                    }
                    
                    .css {
                        width: 85%;
                    }
                    
                    .scss {
                        width: 83%;
                        min-width: 95px;
                    }
                    
                    span {
                        margin-right: $space-x-small;
                        font-size: pxToRem(20.17); 
                        color: $c-white;
                    }
                   
                }
                
            }
            
        }

        #easy_code:hover {
            @include hoverEffect();
        }

    }
    
    .circle_03 {
        position: absolute;
        img {
            width: 42%;
            position: absolute;
            right: 92%;
            top: 77%;
        }
    }
}




// Content ---------------------------------------


