            }
        
            .info__title {
                height: 260px;
            }
        
            .general-title {
                font-size: pxToRem(31);
            }
        
            .content__avatar {
                top: 66px;
                left: 42px;
                max-width: 487px;
                height: 537px;
            }

        }
    
        .about {

            .about{

                &__me :first-child, 
                &__interests {
                    max-width: 440px;
                }

                &__skills{

                    width: 46%;
                    height: 710px;
                    margin-left: -3.5%;

                    &-name div,
                    &-empty div {
                        margin-bottom: $space-medium32;
                    }
        
                    &-name {
                        bottom: 504px;
                    }
                }
                
            }

        }
        
    }
}

